<template>
  <v-container>
    <h2 class="tabelHeader">MSDK Features table</h2>
    <v-row  justify="center">
      <!-- <v-spacer></v-spacer> -->
      <v-col cols="3" offset="6">
      <v-text-field
        v-model="search"
        append-icon="mdi-magnify"
        label="Search"
        single-line
        hide-details
      ></v-text-field>
      </v-col>
    </v-row>

    <v-row  justify="center">
      <v-col cols="3">
        <v-select
          v-model="selectedPlatfroms"
          :items="allPlatforms"
          attach
          chips
          multiple
          label="Plarform"
          @change="changeSelectedPlatfroms">
        </v-select>
      </v-col>
      <v-col cols="3">
        <v-select
          v-model="selectedVideoOperationTypes"
          :items="allVideoOperationTypes"
          attach
          chips
          multiple
          label="Video operation types"
          @change="changeSelectedVideoOperationTypes">
        </v-select>
      </v-col>
      <v-col cols="3">
        <v-select
          v-model="selectedCodecs"
          :items="allCodecs"
          attach
          chips
          multiple
          label="Codecs"
          @change="changeSelectedCodecs">
        </v-select>
      </v-col>
    </v-row>      



<v-row justify="center">
  <v-col cols="12">
    <v-data-table
      :headers="headers"
      :items="desserts"
      :search="search"
      :custom-filter="filterByAllInfo"
      class="elevation-5"
      disable-sort
      item-key="featureName"
      group-by="videoOperationType"
      hide-default-footer
      hide-default-header
      group-expanded      
    >

    <template v-slot:header> 
      <thead>
      <tr>
        <th v-for="(item, index) in headers" :key="item.text + index"> <span  class="tableHeader">{{ item.text }} </span></th>
      </tr>
      </thead>
    </template>
      
      <template v-slot:group="props">
        <tr>
        <td class="type" colspan="3">
          {{ props.group }}
        </td>
        </tr>

        <template v-for="(item, index) in props.items">
          <tr v-if="index == 0 || (index > 0 && props.items[index - 1].codec != item.codec)" v-bind:key="props.group + item.codec + index">            
            <td class="codec" colspan="3"> {{ item.codec }}</td>
          </tr>
          <tr :key="props.group + item.featureName + item.codec">
            <td v-for="(headItem) in headers" :key="headItem.value + props.group + item[headItem.value].featureName">{{ item[headItem.value] }}</td>
          </tr>
        </template>

      </template>
    </v-data-table>
  </v-col>
    </v-row>
  </v-container>
</template>

<style>
.tabelHeader {
  margin: auto;
  font-size: 40px;
  text-align: center;
  padding: 30px;
}
.selectName {
  padding: 10px;
}

.v-card__sssd {
  padding: 0px !important;
  width: 500px;
}
.v-card__text {
  padding: 0px;
}

.codec {
  background-color: #0b1d3112;
  /* background-color: #1b0f020f; */
  text-align: center;
  /* font-size: 35px; */
}

.type {
  background-color: #0b1d3154;
  /* background-color: #1b0f024f; */
  font-weight: bold;
  /* font-size: 40px; */
  text-align: center;
}
.selectTitle {
  text-align: end;
}

.area {
  height: 50px;
}

.tableHeader {
  font-size: 15px;
  color: #000000c7;
}
</style>

<script>
  import json from '../json.json'
  export default {
    name: 'Table',

    data () {
      return {
        selectedPlatfroms: [],
        prevSelectedPlatfroms: [],

        allVideoOperationTypes: ['Encode', 'Decode', 'VP', 'all'],
        selectedVideoOperationTypes: [],
        prevSelectedVideoOperationTypes: [],

        selectedCodecs: [],
        prevSelectedCodecs: [],


        search: '',
        headers: json.headers,
        desserts: json.desserts,
      }
    },
    computed: {
      allPlatforms () {
        var platforms = []
        for (let item of json.headers) {
          if (item.value != 'featureName') {
            platforms.push(item.text)
          }
        }
        platforms.push('all')
        return platforms
      },
      allCodecs () {
        var codecs = []
        for (let item of json.desserts) {
          if (!codecs.includes(item.codec)) {
            codecs.push(item.codec)
          }
        }
        codecs.push('all')
        return codecs
      }
    },

    watch: {

      selectedCodecs (val) {
        if (val.includes('all') || val.length == 0) {
          this.desserts = json.desserts;
        } else {
          var newItems = [];
          for (let item of json.desserts) {
            for (let codec of val) {
              if (item.codec.toLowerCase() == codec.toLowerCase()) {
                newItems.push(item);
                break;
              }
            }
          }
          this.desserts = newItems;
        }
      },
      selectedVideoOperationTypes (val) {

        if (val.includes('all') || val.length == 0) {
          this.desserts = json.desserts;
        } else {
          var newItems = [];
          for (let item of json.desserts) {
            for (let type of val) {
              if (item.videoOperationType.toLowerCase() == type.toLowerCase()) {
                newItems.push(item);
                break;
              }
            }
          }
          this.desserts = newItems;
        }
      },
      selectedPlatfroms (val) {
        if (val.includes('all') || val.length == 0) {
          this.headers = json.headers
        } else {
          var newHeaders = [json.headers[0]];
          for (let item of json.headers) {
            for (let pl of val) {
              if (item.text.toLowerCase() == pl.toLowerCase()) {
                newHeaders.push(item);
                break;
              }
            }
          }
          this.headers = newHeaders;
        }
      }
    },


    methods: {
      filterByAllInfo (value, search, item) {
        return item.codec.toLowerCase().includes(search.toLowerCase()) || item.featureName.toLowerCase().includes(search.toLowerCase()) || item.videoOperationType.toLowerCase().includes(search.toLowerCase())
      },
      changeSelectedCodecs (val) {

        var prev = this.prevSelectedCodecs
        this.prevSelectedCodecs = val

        if (val.includes('all') && !prev.includes('all') && val.length != this.allCodecs.length) {
          this.prevSelectedCodecs = this.allCodecs
          this.selectedCodecs = this.allCodecs;
        } else if (val.includes('all') && prev.includes('all') && val.length != prev.length) {
          const index = val.indexOf('all');
          if (index > -1) {
            val.splice(index, 1);
          }
          this.prevSelectedCodecs = val
          this.selectedCodecs = val
        } else if (!val.includes('all') && prev.includes('all')) {
          this.prevSelectedCodecs = []
          this.selectedCodecs = [];
        } else if (!val.includes('all') && !prev.includes('all') && val.length == this.allCodecs.length - 1) {
          this.prevSelectedCodecs = this.allCodecs
          this.selectedCodecs = this.allCodecs
        }
      },
      changeSelectedVideoOperationTypes (val) {

        var prev = this.prevSelectedVideoOperationTypes
        this.prevSelectedVideoOperationTypes = val

        if (val.includes('all') && !prev.includes('all') && val.length != this.allVideoOperationTypes.length) {
          this.prevSelectedVideoOperationTypes = this.allVideoOperationTypes
          this.selectedVideoOperationTypes = this.allVideoOperationTypes;
        } else if (val.includes('all') && prev.includes('all') && val.length != prev.length) {
          const index = val.indexOf('all');
          if (index > -1) {
            val.splice(index, 1);
          }
          this.prevSelectedVideoOperationTypes = val
          this.selectedVideoOperationTypes = val
        } else if (!val.includes('all') && prev.includes('all')) {
          this.prevSelectedVideoOperationTypes = []
          this.selectedVideoOperationTypes = [];
        } else if (!val.includes('all') && !prev.includes('all') && val.length == this.allVideoOperationTypes.length - 1) {
          this.prevSelectedVideoOperationTypes = this.allVideoOperationTypes
          this.selectedVideoOperationTypes = this.allVideoOperationTypes
        }
      },
      changeSelectedPlatfroms (val) {

        var prev = this.prevSelectedPlatfroms
        this.prevSelectedPlatfroms = val

        if (val.includes('all') && !prev.includes('all') && val.length != this.allPlatforms.length) {
          this.prevSelectedPlatfroms = this.allPlatforms
          this.selectedPlatfroms = this.allPlatforms;
        } else if (val.includes('all') && prev.includes('all') && val.length != prev.length) {
          const index = val.indexOf('all');
          if (index > -1) {
            val.splice(index, 1);
          }
          this.prevSelectedPlatfroms = val
          this.selectedPlatfroms = val
        } else if (!val.includes('all') && prev.includes('all')) {
          this.prevSelectedPlatfroms = []
          this.selectedPlatfroms = [];
        } else if (!val.includes('all') && !prev.includes('all') && val.length == this.allPlatforms.length - 1) {
          this.prevSelectedPlatfroms = this.allPlatforms
          this.selectedPlatfroms = this.allPlatforms
        }
      },
    },
  }
</script>
